<template>
	<div class="messageBox"@click="closeBox()">
		<el-row type="flex"justify="center" style="height: 100vh;"align="middle">
		  <el-col :xs="24" :sm="20" :md="16" :lg="9" :xl="7">
			  <div class="grid-content box">
				  <div class="message"@click.stop="stop">
					  <div>
						  <img src="../assets/imgs/messageBox/4.png" alt=""@click="close()">
						  <span>Import Pool</span>
						  <img src="../assets/imgs/messageBox/3.png" alt="">
					  </div>
					  <div>
						  <p>
							  <span>Tip: </span>
							   Use this tool to find pairs that don't
							  automatically appear in the interface.
						  </p>
						  
						  
					  </div>
					  <div class="my_input one"  @click="open('from')">
						  <div class="left">
							  <img :src="token_from.icon" alt="">
							  <span>{{token_from.txt}}</span>
						  </div>
						  <div class="right">
							  <img src="../assets/imgs/messageBox/5.png" alt="">
						  </div>
						  
					  </div>
					  <div class="my_input two"v-if="token_to===null" @click="open('to')">
					  						  <div class="left">
					  							  
					  							  <span>Select a Token</span>
					  						  </div>
					  						  <div class="right">
					  							  <img src="../assets/imgs/messageBox/5.png" alt="">
					  						  </div>
					  						  
					  </div>
					  <div class="my_input two" v-else  @click="open('to')">
					  						  <div class="left">
					  							  <img :src="token_to.icon" alt="">
					  							  <span>{{token_to.txt}}</span>
					  						  </div>
					  						  <div class="right">
					  							  <img src="../assets/imgs/messageBox/5.png" alt="">
					  						  </div>
					  						  
					  </div>
					  <div class="bottom">
						  Connect to a wallet to find pools
					  </div>
					  
				  </div>
			  </div>
		  </el-col>
		</el-row>
	</div>
</template>

<script>
	export default {
		data() {
			return {
			}
		},
		props:[
			'token_from','token_to'
		],
		methods: {
			stop(){},
			close(){
				this.$emit('closePair','closeBox')
			},
			open(str){
				this.$emit('openBox',str)
			},
			closeBox(){
				this.$emit('closePair','closeBox');
			}

		}
	}
</script>

<style  scoped="scoped">
.messageBox{
	height: 100vh;
}
.grid-content.box{
	padding: 0 3.4%;
	box-sizing: border-box;
	margin:0 auto ;
	
}
.grid-content.box>.message{
	background: #FFFFFF;
	box-shadow: 0px 7px 28px 0px #DADADA;
	border-radius: 20px;
	padding: 4.3vh 5.6% 4vh 5.6%;
	box-sizing: border-box;
}
.grid-content.box>.message>div:nth-child(1){
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.grid-content.box>.message>div:nth-child(1)>span{
	font-size: 20px;
	font-family: ArialRoundedMTBold;
	color: #333333;
}
.grid-content.box>.message>div:nth-child(1) img:hover{
	cursor: pointer;
}
.grid-content.box>.message>div:nth-child(2){
	background: #F6F6F6;
	border-radius: 8px;
	padding: 25px 9% 27px 4.5%;
	margin-top: 4.8vh;
	margin-right: 2%;
}
.grid-content.box>.message>div:nth-child(2)>p:nth-child(1){
	font-size: 16px;
	font-family: PingFangSC-Medium, PingFang SC;
	font-weight: 500;
	line-height: 22px;
	color:#333333;
	word-wrap:break-word;
	word-break:keep-all;
}
.grid-content.box>.message>div:nth-child(2)>p:nth-child(1)>span{
	color: #00C19E;
}
.grid-content.box>.message>div.my_input{
	height: 80px;
	border-radius: 18px;
	border: 1px solid #EAEAEA;
	padding: 2.8vh 8.5% 2.8vh 4.2% ;
	box-sizing: border-box;
	display: flex;
	justify-content: space-between;
	margin-top: 3.3vh;
}
.grid-content.box>.message>div.my_input:hover{
	cursor: pointer;
}
.grid-content.box>.message>div.my_input>.left,.grid-content.box>.message>div.my_input>.right{
	display: flex;
	align-items: center;
}
.grid-content.box>.message>div.my_input>.left>span{
	font-size: 20px;
	font-family: ArialMT;
	color: #333333;
	margin-left: 10px;
}
.grid-content.box>.message>div.my_input>.right{
}


.grid-content.box>.message>div.my_input.two{
	margin-top: 3.5vh;
	padding-left: 3.5%;
}
.grid-content.box>.message>div.bottom{
	text-align: center;
	font-size: 16px;
	font-family: PingFangSC-Regular, PingFang SC;
	font-weight: 400;
	color: #666666;
	margin-top: 56px;
}
</style>


