<template>
    <div class="capital">
		
		<el-row type="flex"justify="center">
		  <el-col :xs="22" :sm="20" :md="18" :lg="16" :xl="14">
			  <div class="grid-content box">
				  <div class="bg">
					  
				  	<p>Liquidity provider rewards</p>
				  	<p>Liquidity providers earn a 0.3% fee on all trades proportional to their share of the pool. Fees are added to the pool, accrue in real time and can be claimed by withdrawing your liquidity.</p>
				  	
				  	<p>Read more about providing liquidity</p>
				  </div>
				  <div class="btns hidden-md-and-down">
					  <div>
						  Your liquidity
					  </div>
					  <div>
						  <button @click="openMessageBox(0)">Create a pair</button>
						  <button  @click="openMessageBox(1)">Add Liquidity </button>
					  </div>
				  </div>
				  <div class="md_show hidden-lg-and-up">
					  <div>Your liquidity</div>
					  <div>
						  <button  @click="openMessageBox(0)">Create a pair</button>
					  </div>
					  <div>
						  <button  @click="openMessageBox(1)">Add Liquidity</button>
					  </div>
				  </div>
				  <div class="txt one">
					  Connect to a wallet to view your liquidity.
				  </div>
				  <div class="txt two">
				  			Don't see a pool you joined? 
									  <span   @click="openMessageBox(2)">Import it.</span>
				  </div>
				  

				  <el-dialog
				    title="tip"
				    :visible.sync="createPair"
					:fullscreen="true"
					@click="closeBox()"
					
				   >
					<message-box @closePair='closeBox' @openBox="open":token_from='token_from':token_to='token_to'></message-box>
				    
				    
				  </el-dialog>
				  <el-dialog
				    title=""
				    :visible.sync="add_liquidity"
				  					:fullscreen="true"
				  					
				   >
				  					
									<add-liquidity @closePair='closeBox'@openBox="open":token_from='token_from':token_to='token_to'></add-liquidity>
				    
				    
				  </el-dialog>
				  <el-dialog
				    title=""
				    :visible.sync="import_pool"
				  					:fullscreen="true"
				  					
				   >
				  					
				  									
													<import-pool @closePair='closeBox'@openBox="open":token_from='token_from':token_to='token_to'></import-pool>
													
				    
				    
				  </el-dialog>
				  <el-dialog
				    title=""
				    :visible.sync="select_token"
				  					:fullscreen="true"
				  					
				   >
				  					
				  									
				  													
				  									<select-token @closePair='closeSeleBox'@select="checked":fromIndex="from_index":toIndex="to_index"></select-token>
				  													
				    
				    
				  </el-dialog>
				  
				  
				  
				  
			  </div>
			  
					
						
				
				</el-col>
		</el-row>
		

    </div>
</template>

<script>
	
	import messageBox from '../../components/messageBox.vue'
	import addLiquidity from '../../components/addLiquidity.vue'
	import importPool from '../../components/importPool.vue'
	import selectToken from '../../components/selectToken.vue'
	import $ from 'jquery';
	
    export default {
		mounted() {
			 
		},
        data() {
            return {
				createPair:false,
				add_liquidity:false,
				import_pool:false,
				select_token:false,
				token_from:{
					icon:require('../../assets/imgs/messageBox/2.png'),
					txt:'ETH'
				},
				token_to:null,
				str:'',
				from_index:0,
				to_index:0,
            }
        },
		

        methods: {
			
			checked(e){
				console.log(e);
				let {str} = this;
				let {item,index} = e;
				if(str==='from'){
					this.token_from = item;
					this.from_index = index;
					
				}
				else{
					this.token_to = item;
					this.to_index = index;
				}
				console.log(this.from_index,this.to_index)
				this.select_token=false;
			},
			scroll(){
				
				
				
			},
			
			openMessageBox(num){
				
				if(num==0){
					this.createPair = true;
				}
				else if(num==1){
					this.add_liquidity=true;
				}
				else{
					this.import_pool=true;
				}
				
				
				
				
			},
			
			
			closeBox(e){
				
				console.log(e);
				this.createPair=false;
				this.add_liquidity=false;
				this.import_pool = false;
				
				
				
			},
			closeSeleBox(){
				this.select_token=false;
				// let $header = $('.header').eq(0);
				// $header.css('paddingRight','0px')
			},
			open(e){
				
				console.log(e);
				this.str = e;
				console.log(this.str);
				this.select_token=true;
				// let $header = $('.header').eq(0);
				// $header.css('paddingRight','17px');
			}

        },
		components:{
			'message-box':messageBox,
			'add-liquidity':addLiquidity,
			'import-pool':importPool,
			'select-token':selectToken
		}
    }
</script>

<style src="./capital.css" scoped="scoped">

</style>


