<template>
	<div class="header":class="[theme ? 'white' : 'black',{ 'navActive': scrollFlag},num==0?'navActive':'']">
		<el-row style="">
		  
		  <el-col :xs="12" :sm="12" :md="12" :lg="4":xl="3" style="display: flex;justify-content: flex-end;">
			  <div class="grid-content icon hidden-md-and-down"@click="active(0,'/index')">
				<img src="../assets/imgs/header/icon.png" alt="">
				<span>as.finance</span>
			  </div>
		   </el-col>
		   <el-col :xs="0" :sm="0" :md="0" :lg="0":xl="1">
		   			  <div class="grid-content icon" style="">
		   				&nbsp;
		   			  </div>
		    </el-col>
		   
		  <el-col :xs="4" :sm="6" :md="8" :lg="20" :xl="19">
			  <div class="grid-content nav hidden-md-and-down">
				  <div class="left">
					  <div class="span" v-for="(item,index) in navs":class="num==index?'act':''"@click="active(index,item.url)">{{item.tit}}</div>
					  
				  </div>
				  <div class="right">
					  
					  <el-dropdown  trigger="click" placement="bottom">
					    <button class="meau_btns">
					    						  <img :src="drops[drop_index].icon" alt="">
					    						  <span>{{drops[drop_index].txt}}</span>
					    </button>
					    <el-dropdown-menu slot="dropdown" style="width: 215px;margin-top: 29px;"class="my_drop hidden-md-and-down">
							<el-dropdown-item disabled style="color: red;

font-size: 16px;
font-family: PingFangSC-Medium, PingFang SC;
font-weight: 500;padding-left: 56px;line-height: 50px;font-weight: bold;
color: #333333;
">Chain</el-dropdown-item>
					      <el-dropdown-item class="my_drop_item" v-for="(item,index) in drops":key="index"@click.native='drop_handel(index)'>
							  <div>								  <img :src="item.icon" alt=""></div>
							  <div>							 {{item.txt}}</div>
							  
						  </el-dropdown-item>
					    </el-dropdown-menu>
					  </el-dropdown>
					  <button>
					  						  
					  						  <span>AS</span>
					  </button>
					  <button>
						  Connect Wallet
					  </button>
					  <!-- <button @click="changeTheme()">
					  			<img src="../assets/imgs/header/icon-top-theme.png" alt="">		
					  </button> -->
					  <button>
					  			<img src="../assets/imgs/header/icon-top-menu.png" alt="">		
					  </button>
				  </div>
			  
			  
			  </div>
			  
		  </el-col>
		  <el-col :xs="24" :sm="24" :md="24" :lg="0":xl="0">
		  			  <div class="grid-content meau">
						  <div class="meau_left"@click="active(0,'/index')">
							 <img src="../assets/imgs/header/icon.png" alt=""> 
							 <span>as.finance</span>
						  </div>
						  <div class="meau_right"@click="drawer = true">
						  		<img src="../assets/imgs/header/ego-caidan.png" alt="">					  
						  </div>
		  			  </div>
		   </el-col>
		   
		  
			
		</el-row>
		<el-drawer
		  title=""
		  :visible.sync="drawer"
		  size="50%"
		  :modal="false"
		  direction='ltr'
		  :with-header="false" class="hidden-lg-and-up">
		  <div class="meau_item ">
			  
			  <div class="items">
				  <div class="meau_icon"@click="active(0,'/index')">
					  <img src="../assets/imgs/logo/logo.png" alt="">
				  </div>
				  <div class="meau_nav">
					  <div v-for="(item,index) in navs"@click="active(index,item.url)":class="num==index?'act':''">{{item.tit}}</div>
				  </div>
				  <div class="meau_btn">
					  
				  </div>
				  
			  </div>
		  </div>
		  
		  
		</el-drawer>
	</div>
</template>

<script>
	import {mapState,mapMutations} from 'vuex'
	export default {
		mounted() {
			// window.addEventListener('scroll', this.handleScroll);
			let idx = sessionStorage.getItem('index');
			
			console.log(idx);
			if(idx===null||idx===""){
				
			}
			else{
				this.num = idx;
				
			}
			this.$router.afterEach((to,from)=>{
				let {path} = to;
				console.log(path);
				
				if(path==='/mining'){
					this.num=4;
					sessionStorage.setItem('index',4);
				}
			})
		},
		computed:{
			...mapState(['theme','userName'])
		},
		data() {
			return {
				drop_index:0,
				drops:[
					{
						icon:require('../assets/imgs/dropdown/Image1.png'),
						txt:'Ethereum'
					},
					{
						icon:require('../assets/imgs/dropdown/Image4.png'),
						txt:'Huobi ECO chain'
					},
					{
						icon:require('../assets/imgs/dropdown/Image2.png'),
						txt:'OKEXchain'
					},
					{
						icon:require('../assets/imgs/dropdown/Image3.png'),
						txt:'Binance smart chain'
					},
					{
						icon:require('../assets/imgs/dropdown/Image5.png'),
						txt:'Tronchain'
					},
				],
				
				scrollFlag:false,
				drawer: false,
				num:0,
				navs:[
					{
						tit:'',
						url:'/index'
					},
					{
						tit:'',
						url:'/exchange'
					},
					{
						tit:'',
						url:'/capital'
					},
					{
						tit:''
					},
					{
						tit:'',
						url:'/mining'
					},
				]
			}
		},
		
		methods: {
			
			...mapMutations(['btn','setName','changeTheme']),
			drop_handel(i){
				console.log(i);
				this.drop_index = i;
			},
			active(i,url){
				if(i==3){
					
				}
				else{
					this.num=i;
					sessionStorage.setItem('index',i);
					this.$router.push(url)
				}
				
			},
			close(){
				this.drawer=false;
			},
			handleScroll () {
			  let _this=this;
			  var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
			  // console.log(scrollTop)
			  if(scrollTop){
			    _this.scrollFlag=true
			  }else{
			    _this.scrollFlag=false
			  }
			}
			

		}
	}
</script>

<style scoped="scoped">
	.header{
		/* position: fixed; */
		/* top: 0;left: 0; */
		width: 100%;
		box-sizing: border-box;
		z-index: 999;
		height: 80px;
		background: #F3F3F3;
		border-bottom: 1px solid #E7E7E7;
	}
	.header.navActive{
		/* background: red; */
		    /* border-bottom: 1px solid rgb(250, 250, 250); */
			/* transition: background-color 0.25s ease 0s; */
			background-image: url(../assets/imgs/index/bg.png);
			background-repeat: no-repeat;
			background-position: left top;
			border-color: #FFFFFF;
	} 
	.header .white{
		color: #333333;
	}
	.header .grid-content.icon{
		width: 160px;
		height: 80px;
		display: flex;
		align-items: center;
		
	}
	.header .grid-content.nav{
		height: 80px;
		display: flex;
		justify-content: space-between;
	}
	.header  .grid-content.icon img{
		width: 40px;height: 40px;		margin-bottom: 3px;
	}
	.header  .grid-content.icon:hover{
		cursor: pointer;
	}
	.header  .grid-content.icon>span{
		font-size: 22px;
		margin-left: 7px;
		font-family: ArialMT;
		color: #01C19E;
		margin-bottom: 3px;
		font-weight: bold;
	}
	.header  .grid-content.nav>.left{
		line-height: 80px;
		display: inline-flex;
		width: 33.3%;
		padding-left: 30px;
		justify-content: space-around;
		box-sizing: border-box;
	}
	.header  .grid-content.nav>.left>.span{
		display: inline-block;
		font-size: 16px;
		font-family: PingFangSC-Regular, PingFang SC;
		font-weight: 400;
		white-space: nowrap;
	}
	.header  .grid-content.nav>.left>.span.act{
		font-size: 16px;
		font-family: PingFangSC-Medium, PingFang SC;
		color: #00C19E;
	}
	.header  .grid-content.nav>.left>.span:hover{
		cursor: pointer;
	}
	.header  .grid-content.nav>.right{
		display: flex;
		align-items: center;
	}
	.header  .grid-content.nav>.right button{
		display: flex;
		align-items: center;
		padding: 9px 18px;
		box-sizing: border-box;		border: none;
		margin-right: 10px;
	}
	.header  .grid-content.nav>.right button:focus{
		outline: none;
		
	}
	.header  .grid-content.nav>.right button:nth-child(1){
		/* width: 192px; */
		height: 40px;
		background: #00C19E;
		border-radius: 22px;
	}
	.header  .grid-content.nav>.right button:nth-child(1) img{
		width: 20px;
		height: 20px;
		margin-right: 8px;
	}
	.header  .grid-content.nav>.right button:nth-child(1)>span{
		font-size: 14px;
		font-family: PingFangSC-Regular, PingFang SC;
		font-weight: 400;
		color: #FFFFFF;
	}
	.header  .grid-content.nav>.right>button:nth-child(2){
		width: 50px;
		height: 40px;
		background: linear-gradient(135deg, #00C19E 0%, #C8D357 100%);
		border-radius: 8px;
		font-size: 14px;
		font-family: PingFangSC-Medium, PingFang SC;
		font-weight: 500;
		color: #FFFFFF;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.header  .grid-content.nav>.right>button:nth-child(3){
		width: 145px;
		height: 40px;
		background: rgba(0, 193, 158, 0.24);
		border-radius: 8px;
		font-size: 14px;
		font-family: PingFangSC-Regular, PingFang SC;
		font-weight: 400;
		color: #00C19E;
	}
	.header  .grid-content.nav>.right>button:nth-child(4),.header  .grid-content.nav>.right>button:nth-child(5){
		width: 40px;
		height: 40px;
		background: #FFFFFF;
		border-radius: 8px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.header  .grid-content.nav>.right>button:nth-child(4) img,.header  .grid-content.nav>.right>button:nth-child(5) img{
		width: 22px;
		height: 22px;
	}
	.header  .grid-content.nav>.right>button:hover{
		cursor: pointer;
	}
	.header  .grid-content.meau{
		height: 80px;
		display: flex;
		justify-content: space-between;
		padding: 0 32px  29px;
		box-sizing: border-box;
	}
	.header  .grid-content.meau>.meau_left{
			height: 80px;
		display: flex;
		align-items: center;
	}
	.header  .grid-content.meau>.meau_left:hover{
		cursor: pointer;
	}
	.header  .grid-content.meau>.meau_right{
				height: 80px;	
		display: flex;
		align-items: center;
		
	}
	.header  .grid-content.meau>.meau_left img{
		width: 40px;
		margin-right: 7px;
		height: 40px;
	}
	.header  .grid-content.meau>.meau_left>span{
		font-size: 22px;
		font-family: ArialMT;
		color: #01C19E;
		font-weight: bold;
	}
	.header  .grid-content.meau>.meau_right img{
		width: 32px;height: 32px;
	}
	.cancel{
		/* background-color: red; */
		text-align: right;
	}
	.cancel img:hover{
		cursor: pointer;
	}
	.meau_item{
	}
	.meau_item>.items>.meau_nav{
		padding-left: 10%;
		padding-top: 20px;
		box-sizing: border-box;
	}
	.meau_item>.items>.meau_nav>div{
		    font-size: 16px;
			color: #333;
			font-family: "GT Haptik Regular";
			margin-bottom: 30px;
			font-weight: 550;
	}
	.meau_item>.items>.meau_nav>div:hover{
		cursor: pointer;
	}
	.meau_item>.items>.meau_nav>div.act{
		color: #00C19E;
	}
	.meau_right:hover{
		cursor: pointer;
	}
	.meau_item>.items>.meau_icon{
		display: flex;
		align-items: center;
		justify-content: center;
		height: 100px;
		border-bottom: 1px solid #efefef;
	}
	
</style>



