<template>
	<div class="exchange">
		
		<el-row type="flex"justify="center">
		  <el-col :xs="23" :sm="20" :md="16" :lg="10" :xl="8">
			  <div class="grid-content box one">
				  <div class="top">
					  <span>ASswap</span>
					  Defi
					  <span></span>
					  
				  </div>
				  
				  
			  </div>
		  </el-col>
		</el-row>
		<el-row type="flex"justify="center">
		  <el-col :xs="24" :sm="20" :md="16" :lg="9" :xl="7">
			  <div class="grid-content box two">
				  
				  <div class="bottom">
					  <div class="message">
					  					  
					  					  <div class="tit">
											  Swap
										  </div>
					  					  <div class="my_input one">
					  						  <div class="left">
					  							  <p>From</p>
					  							  <p>
					  								  <input type="text"placeholder="0.0">
					  							  </p>
					  						  </div>
					  						  <div class="right" @click="sele('from')">
													  <img :src="token_from.icon" alt="">
													  <span>{{token_from.txt}}</span>
													  <img src="../../assets/imgs/messageBox/5.png" alt="">
					  							  
					  						  </div>
					  					  </div>
					  					  <div class="my_input two">
					  					  						  <div class="left">
					  					  							  <p>To</p>
					  					  							  <p>
					  					  								  <input type="text"placeholder="0.0">
					  					  							  </p>
					  					  						  </div>
																  <div class="right"v-if="token_to===null"@click="sele('to')">
																  																	  
																  					  												  <button>
																  					  													  <span></span>
																  					  													  <img src="../../assets/imgs/messageBox/6.png" alt="">
																  					  												  </button>
																  </div>
																  <div class="right" v-else @click="sele('to')">
																  													  <img :src="token_to.icon" alt="">
																  													  <span>{{token_to.txt}}</span>
																  													  <img src="../../assets/imgs/messageBox/5.png" alt="">
																  					  							  
																  </div>
					  					  						  
					  					  </div>
					  					  <div class="btn">
					  						  <button>Connect Wallet</button>
					  					  </div>
										  <el-dialog
										    title=""
										    :visible.sync="select_token"
										  					:fullscreen="true"
										  					
										   >
										  					
										  									
										  													
																							<select-token @closePair='closeBox'@select="checked":fromIndex="from_index":toIndex="to_index":type="type"></select-token>
										  													
										    
										    
										  </el-dialog>
					  </div>
				  </div>
				  
			  </div>
		  </el-col>
		</el-row>
	</div>
</template>

<script>
	import selectToken from '../../components/selectToken.vue';
	import $ from 'jquery';
	export default {
		data() {
			return {
				select_token:false,
				token_from:{
					icon:require('../../assets/imgs/messageBox/2.png'),
					txt:'ETH'
				},
				token_to:null,
				str:'',
				from_index:0,
				to_index:null,
				type:null
					
				
			}
		},
		
		methods: {
			closeBox(){
				this.select_token=false;
				
			},
			checked(e){
				console.log(e);
				let {str} = this;
				let {item,index} = e;
				if(str==='from'){
					this.token_from = item;
					this.from_index = index;
					
				}
				else{
					this.token_to = item;
					this.to_index = index;
				}
				console.log(this.from_index,this.to_index)
				this.select_token=false;
			},
			sele(str){
				this.select_token = true;
				this.str = str;
				console.log(this.str);
				this.type=str;
				
				
			}

		},
		components:{
			'select-token':selectToken
		}
	}
</script>

<style src="./exchange.css" scoped="scoped">

</style>


