import App from '../App'
import Home from '../views/home/home.vue'
import Login from '../views/login/login.vue'
import index from '../views/index/index.vue'
import pledge from '../views/pledge/pledge.vue'
import capital from '../views/capital/capital.vue'
import exchange from '../views/exchange/exchange.vue'
import mining from '../views/mining/mining.vue'

export default [
  {
    path: '/',
    component: App, // index.html
    children: [
      //App.vue
      // home
      {
        path: '',
        redirect: '/home',
      },
      {
        path: '/home',
        component: Home,
		children: [
			{
			  path: '',
			  redirect: '/index',
			},
			{
			  path: '/index',
			  component: index,
			},
			{
			  path: '/pledge',
			  component: pledge,
			},
			
			{
			  path: '/capital',
			  component:capital,
			},
			{
			  path: '/exchange',
			  component:exchange,
			},
			{
			  path: '/mining',
			  component:mining,
			},
			
		]
      },
      // 
      {
        path: '/login',
        component: Login,
      },
	  
    ],
  },
  
]



