<template>
	<div class="messageBox"@click="closeBox()">
		<el-row type="flex"justify="center" style="height: 100vh;"align="middle">
		  <el-col :xs="24" :sm="20" :md="16" :lg="9" :xl="7">
			  <div class="grid-content box">
				  <div class="message"@click.stop="stop">
					  <div>
						  <img src="../assets/imgs/messageBox/4.png" alt=""@click="close()">
						  <span>Add Liquidity</span>
						  <img src="../assets/imgs/messageBox/3.png" alt="">
					  </div>
					  <div>
						  <p>
							  <span>Tip: </span>
							  When you add liquidity, you will receive pool tokens representing your position. These tokens automatically earn fees proportional to your share of the pool, and can be redeemed at any time.
						  </p>
						  
						  
					  </div>
					  <div class="my_input one">
					  						  <div class="left">
					  							  <p>Input</p>
					  							  <p>
					  								  <input type="text"placeholder="0.0">
					  							  </p>
					  						  </div>
					  						  <div class="right"  @click="open('from')">
					  							  <img :src="token_from.icon" alt="">
					  							  <span>{{token_from.txt}}</span>
					  							  <img src="../assets/imgs/messageBox/5.png" alt="">
					  						  </div>
					  </div>
					  <div class="my_input two">
					  						  <div class="left">
					  							  <p>Input</p>
					  							  <p>
					  								  <input type="text"placeholder="0.0">
					  							  </p>
					  						  </div>
					  						  <div class="right"v-if="token_to===null" @click="open('to')">
					  												  <button>
					  													  <span></span>
					  													  <img src="../assets/imgs/messageBox/6.png" alt="">
					  												  </button>
					  						  </div>
					  											  <div class="right" v-else  @click="open('to')">
					  											  													  <img :src="token_to.icon" alt="">
					  											  													  <span>{{token_to.txt}}</span>
					  											  													  <img src="../assets/imgs/messageBox/5.png" alt="">
					  											  					  							  
					  											  </div>
					  </div>
					  <div class="btn">
						  <button>Connect Wallet</button>
					  </div>
				  </div>
			  </div>
		  </el-col>
		</el-row>
	</div>
</template>

<script>
	export default {
		data() {
			return {
			}
		},
		props:[
			'token_from','token_to'
		],
		methods: {
			stop(){},
			close(){
				this.$emit('closePair','closeBox')
			},
			open(str){
				this.$emit('openBox',str)
			},
			closeBox(){
				this.$emit('closePair','closeBox');
			}

		}
	}
</script>

<style  scoped="scoped">
.messageBox{
	height: 100vh;
}
.grid-content.box{
	padding: 0 3.4%;
	box-sizing: border-box;
	margin:0 auto ;
	
}
.grid-content.box>.message{
	background: #FFFFFF;
	box-shadow: 0px 7px 28px 0px #DADADA;
	border-radius: 20px;
	padding: 4.3vh 5.6% 5.1vh 5.6%;
	box-sizing: border-box;
}
.grid-content.box>.message>div:nth-child(1){
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.grid-content.box>.message>div:nth-child(1)>span{
	font-size: 20px;
	font-family: ArialRoundedMTBold;
	color: #333333;
}
.grid-content.box>.message>div:nth-child(1) img:hover{
	cursor: pointer;
}
.grid-content.box>.message>div:nth-child(2){
	background: #F6F6F6;
	border-radius: 8px;
	padding: 18px 9% 30px 4.5%;
	margin-top: 4.8vh;
	margin-right: 2%;
}
.grid-content.box>.message>div:nth-child(2)>p:nth-child(1){
	font-size: 16px;
	font-family: PingFangSC-Medium, PingFang SC;
	font-weight: 500;
	line-height: 22px;
	color:#333333;
	word-wrap:break-word;
	word-break:keep-all;
}
.grid-content.box>.message>div:nth-child(2)>p:nth-child(1)>span{
	color: #00C19E;
}
.grid-content.box>.message>div.my_input{
	height: 80px;
	border-radius: 18px;
	border: 1px solid #EAEAEA;
	padding: 12px 8.5% 13px 4.5% ;
	box-sizing: border-box;
	display: flex;
	justify-content: space-between;
	margin-top: 2.9vh;
}
.grid-content.box>.message>div.my_input.two{
}
.grid-content.box>.message>div.my_input>.left{
	/* background-color: red; */
}
.grid-content.box>.message>div.my_input>.left>p:nth-child(1){
	font-size: 16px;
	font-family: ArialMT;
	color: #333333;
}
.grid-content.box>.message>div.my_input>.left>p:nth-child(2){
}
.grid-content.box>.message>div.my_input>.left>p:nth-child(2)>input{
	font-size: 24px;
	margin-top: 5px;
	border: none;
	caret-color:#ccc;
	color: #ccc;
	display: inline-block;
	width: 100%;
}
.grid-content.box>.message>div.my_input>.left>p:nth-child(2)>input::-webkit-input-placeholder {
    /* WebKit browsers */
    color: #ccc;
}
.grid-content.box>.message>div.my_input>.left>p:nth-child(2)>input::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #ccc;
}
.grid-content.box>.message>div.my_input>.left>p:nth-child(2)>input:-ms-input-placeholder {
    /* Internet Explorer 10+ */
    color: #ccc;
}
.grid-content.box>.message>div.my_input>.right{
	display: flex;
	align-items: center;
	padding-top: 15px;
	box-sizing: border-box;
}
.grid-content.box>.message>div.my_input>.right>span{
	font-size: 20px;
	font-family: ArialMT;
	color: #333333;
	white-space: nowrap;
	margin-left: 10px;
	margin-right: 10px;
}
.grid-content.box>.message>div.my_input img{
}
.grid-content.box>.message>div.my_input.two>.right{
	padding-top: 0;
}
.grid-content.box>.message>div.my_input>.right:hover{
	cursor: pointer;
}
.grid-content.box>.message>div.my_input.two>.right>button{
	width: 114px;
	
	background: #00C09C;
	border-radius: 8px;
	padding: 1vh 7px;
	margin-right: -8px;
}
.grid-content.box>.message>div.my_input.two>.right>button>span{
	font-size: 16px;
	font-family: PingFangSC-Regular, PingFang SC;
	font-weight: 400;
	color: #FFFFFF;
	margin-right: 10px;
}
.grid-content.box>.message>div.my_input.two{
	margin-top: 3.5vh;
}
.grid-content.box>.message>div.btn>button{
	padding: 2.7vh 0px;
	background: #00C19E;
	border-radius: 18px;
	font-size: 16px;
	font-family: ArialMT;
	color: #FFFFFF;
	display: inline-block;
	width: 100%;
	margin-top: 3.3vh;
}
</style>


