<template>
	<div class="selectToken"@click="closeBox()">
		<el-row type="flex"justify="center" style="height: 100vh;"align="middle">
		  <el-col :xs="24" :sm="20" :md="16" :lg="9" :xl="7">
			  <div class="grid-content box">
				  <div class="message"@click.stop="stop">
					  <div class="top">
						  <span>Select a token</span>
						  <img src="../assets/imgs/selectToken/close.png" alt=""@click="close()">
					  </div>
					  <div class="search">
						  <input type="text" placeholder="Search name or paste address">
					  </div>
					  <div class="bottom">
						  <div class="items">
							  <table>
								  <tr v-for="(item,index) in items":key="index"@click="active(index,item)":class="fromIndex==index||toIndex==index?'selected':''">
								  	<td valign="middle"><img :src="item.icon" alt=""></td>
									<td valign="middle">{{item.txt}}</td>
								  </tr>
							  </table>
							  
						  </div>
					  </div>
				  </div>
			  </div>
		  </el-col>
		</el-row>
	</div>
</template>

<script>
	export default {
		props:['fromIndex','toIndex','type'],
		data() {
			return {
				num:0,
				items:[
					{
						icon:require('../assets/imgs/selectToken/1.png'),
						txt:'AS'
					},
					{
						icon:require('../assets/imgs/selectToken/3.png'),
						txt:'BTC'
					},
					{
						icon:require('../assets/imgs/selectToken/2.png'),
						txt:'ETH'
					},
					{
						icon:require('../assets/imgs/selectToken/4.png'),
						txt:'HUSD'
					},
					{
						icon:require('../assets/imgs/selectToken/5.png'),
						txt:'HBTC'
					},
					{
						icon:require('../assets/imgs/selectToken/6.png'),
						txt:'WHT'
					},
					{
						icon:require('../assets/imgs/selectToken/10.png'),
						txt:'USDT'
					},
					{
						icon:require('../assets/imgs/selectToken/7.png'),
						txt:'BNB'
					},
					{
						icon:require('../assets/imgs/selectToken/8.png'),
						txt:'OKB'
					},
					{
						icon:require('../assets/imgs/selectToken/6.png'),
						txt:'HT'
					},
					{
						icon:require('../assets/imgs/selectToken/1.png'),
						txt:'AS'
					},
				]
			}
		},
		
		methods: {
			stop(){},
			close(){
				this.$emit('closePair','closeBox');
			},
			active(i,obj){
				let {fromIndex,toIndex,type} = this;
				if(i==fromIndex||i==toIndex){
											return false;
				}
					// if(type==='from'){
					// 					if(i==fromIndex){
					// 						return false;
					// 					}
					// 					else{
											
					// 					}
					// 				}
					// 				else{
					// 					if(i==toIndex){
					// 						return false;
					// 					}
					// 					else{
											
					// 					}
					// 				}
				
				this.$emit('select',{index:i,item:obj});
				
			},
			closeBox(){
				this.$emit('closePair','closeBox');
			}

		}
	}
</script>

<style  scoped="scoped">
.selectToken{
	height: 100vh;
}
.grid-content.box{
	padding: 0 3.4%;
	box-sizing: border-box;
	
}
.grid-content.box>.message{
	background: #FFFFFF;
	height: 70vh;
	box-shadow: 0px 7px 28px 0px #DADADA;
	border-radius: 20px;
	padding: 40px 5.8% 39px 5.4%;
	
}
.grid-content.box>.message>.top{
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.grid-content.box>.message>.top>span{
	font-size: 20px;
	font-family: ArialRoundedMTBold;
	color: #333333;
}
.grid-content.box>.message>.top img:hover{
	cursor: pointer;
}
.grid-content.box>.message>.search{
	margin-top: 20px;
	width: 444px;
	height: 52px;
	border-radius: 18px;
	border: 1px solid #EAEAEA;
	padding-left: 6.3%;
	display: flex;
	align-items: center;
	box-sizing: border-box;
	width: 100%;
}
.grid-content.box>.message>.search>input{
	font-size: 16px;
	font-family: ArialMT;
	color: #999999;
	display: inline-block;
	border: none;
	width: 100%;
}
.grid-content.box>.message>.bottom{
	height: 49vh;
	border-radius: 18px;
	margin-top: 29px;
	overflow-y: scroll;
	padding-right: 2%;
	box-sizing: border-box;
}
.grid-content.box>.message>.bottom>.items{
	
	background: #F7F7F7;
	border-radius: 18px;
	box-sizing: border-box;
}
.grid-content.box>.message>.bottom::-webkit-scrollbar {

background: #F7F7F7;
border-radius: 10px;
width: 8px;
}
.grid-content.box>.message>.bottom::-webkit-scrollbar-thumb {
width: 8px;
height: 137px;
background: #00C19E;
border-radius: 10px;
}
.grid-content.box>.message>.bottom>.items table{
	border-spacing: 0;
	width: 100%;
}
.grid-content.box>.message>.bottom>.items table tr{
	height: 45px;
}
.grid-content.box>.message>.bottom>.items table tr.selected{
	opacity: 0.3;
}
.grid-content.box>.message>.bottom>.items table tr.selected:hover{
	background: none;
	cursor: default;
}
.grid-content.box>.message>.bottom>.items table tr td:nth-child(1){
	text-align: center;
	padding-top: 5px;
	width: 20px;
	padding-left: 28px;
	padding-right: 10px;
	box-sizing: border-box;
}
.grid-content.box>.message>.bottom>.items table tr:hover{
	background: #F0F0F0;
}
.grid-content.box>.message>.bottom>.items table tr:hover{
	cursor: pointer;
}
.grid-content.box>.message>.bottom>.items table tr td:nth-child(1) img{
	
}
.grid-content.box>.message>.bottom>.items table tr td:nth-child(2){
	box-sizing: border-box;
	text-align: left;
	font-size: 16px;
	font-family: PingFangSC-Regular, PingFang SC;
	font-weight: 400;
	color: #333333;
}

</style>


